/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */

.container-fluid {
  
}

.deck-stats{
    width:25%;
    padding:5px;
    padding-top:0px;
    margin: 0px;
    display: inline-block;
}

#dismiss {
    width: 35px;
    height: 35px;
    line-height: 35px;
    text-align: center;
    background: #011627;
    border: solid 2px #D68FD6;
    display: inline-block;
    position: absolute;
    border-radius:.25rem;
    z-index: 10;
    right: 5px; 
    color: #FFF8F0;
    cursor: pointer;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

#dismiss:hover {
    color: #FFF8F0;
    background: #D68FD6;
}

#sidebar {
    width: 250px;
    position: fixed;
    overflow-y: scroll;
    top: 0px;
    left: 0;
    height: 100vh;
    z-index: 50;
    background: #011627;
    color: #FFF8F0;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;

}




.wrapper {
    display: flex;
    width: 100%;
}

#sidebar.active {

}

#sidebar.inactive {
    margin-left: -250px;
    display: none;
}

#sidebar .sidebar-header {
    padding: 20px;
    background: #011627;
}

#sidebar ul.components {
    padding: 20px 0;
    border-bottom: 1px solid #47748b;
}

#sidebar ul p {
    color: #FFF8F0;
    padding: 10px;
}

#sidebar ul li a {
    padding: 10px;
    font-size: 1.1em;
    display: block;
}


#sidebar ul li a:hover {
    color: #7386D5;
    background: #fff;
}

#sidebar ul li.active>a,
a[aria-expanded="true"] {
    color: #FFF8F0;
    background: #011627;
}

a[data-toggle="collapse"] {
    position: relative;
}

.dropdown-toggle::after {
    display: block;
    position: absolute;
    top: 50%;
    right: 20px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

ul ul a {
    font-size: 0.9em !important;
    padding-left: 30px !important;
    background: #011627;
}

ul.CTAs {
    padding: 20px;
}

ul.CTAs a {
    text-align: center;
    font-size: 0.9em !important;
    display: block;
    border-radius: 5px;
    margin-bottom: 5px;
}

a.download {
    background: #fff;
    color: #7386D5;
}

a.article,
a.article:hover {
    background: #D68FD6 !important;
    color: #FFF8F0 !important;
}
/* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */

#content {
    width: calc(100% - 250px);
    padding: 15px;
    padding-top: 50px;
    min-height: 100vh;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    position: absolute;
    top: 0;
    right: 0;
}

#content.active {
    width: 100%;
}

